  .sky {
    position: absolute;
    width: 100%; height: 100%;
    background-color: #D3C7C0;
  }
  .sky:before,
  .sky:after { position: absolute; content: ""; display: block; }
  
  .sky:before {
    top: 20px; left: 20px;
    width: calc(100% - 40px); height: calc(100% - 40px);
    border: dashed 5px #635256;
    border-radius: 30px;
    box-sizing: border-box;
    opacity: 0.3;
  }
  .sky:after {
    width: 400px; height: 400px;
    top: calc(50% - 200px); left: calc(50% - 200px);
    border-radius: 50%;
    background-color: #635256;
  }
  
  
    /****************************************
     * effect
     ****************************************/
    .firework {
      position: absolute;
      width: 400px; height: 400px;
      border-radius: 50%;
      overflow: hidden;
    }
    .firework div {
      position: absolute;
      top: 0; left: 0;
      width: 100%; height: 100%;
    }
    .firework div:after {
      content: ""; display: block;
      position: absolute;
      opacity: 0;
    }
    .firework div:nth-child(1) { -webkit-transform: rotate(0deg); transform: rotate(0deg);   }
    .firework div:nth-child(2) { -webkit-transform: rotate(45deg); transform: rotate(45deg);  }
    .firework div:nth-child(3) { -webkit-transform: rotate(90deg); transform: rotate(90deg);  }
    .firework div:nth-child(4) { -webkit-transform: rotate(135deg); transform: rotate(135deg); }
    .firework div:nth-child(5) { -webkit-transform: rotate(180deg); transform: rotate(180deg); }
    .firework div:nth-child(6) { -webkit-transform: rotate(225deg); transform: rotate(225deg); }
    .firework div:nth-child(7) { -webkit-transform: rotate(270deg); transform: rotate(270deg); }
    .firework div:nth-child(8) { -webkit-transform: rotate(315deg); transform: rotate(315deg); }
  
  
    /*** type_1 ***/
    .type_1 {
      top: calc(50% - 200px); left: calc(50% - 200px);
    }
    .type_1 div:after {
      top: 5px; left: calc(50% - 20px);
      border-top: solid 70px #635256;
      border-left: solid 20px transparent;
      border-right: solid 20px transparent;
      -webkit-transform: translateY(130px);
              transform: translateY(130px);
    }
  
  /*** type_2 ***/
  .type_2 {
    top: 50%; left: calc(50% - 400px);
  }
  .type_2 div:after {
    top: 0px; left: calc(50% - 40px);
    width: 40px; height: 40px;
    background-color: #8db3b1;
    border-radius: 50%;
    -webkit-transform: translateY(170px);
            transform: translateY(170px);
  }
  
  /*** type_3 ***/
  .type_3 {
    top: calc(50% - 400px); left: 50%;
  }
  .type_3 div:after {
    top: 0px; left: calc(50% - 20px);
    width: 40px; height: 40px;
    background-color: #9994A6;
    -webkit-transform: translateY(150px);
            transform: translateY(150px);
  }
  
  
  /***************************************************************************************************
   *  animation
   ***************************************************************************************************/
  
  /*** sky ***/
  .sky:after { -webkit-animation: hidden-ball 0.6s ease 0s forwards; animation: hidden-ball 0.6s ease 0s forwards; }
  
  /*** type_1 ***/
  .type_1 div:after {
    -webkit-animation:
      fadein 0.3s ease 0.5s forwards,
      show-type-1 0.6s ease 0.5s forwards;
            animation:
      fadein 0.3s ease 0.5s forwards,
      show-type-1 0.6s ease 0.5s forwards;
  }
  
  /*** type_2 ***/
  .type_2 { -webkit-animation: rotate360 4s ease 0.6s forwards; animation: rotate360 4s ease 0.6s forwards; }
  .type_2 div:after {
    -webkit-animation:
      fadein 0.3s ease 1.1s forwards,
      show-type-2 0.6s ease 1.1s forwards;
            animation:
      fadein 0.3s ease 1.1s forwards,
      show-type-2 0.6s ease 1.1s forwards;
  }
  
  /*** type_3 ***/
  .type_3 div:after {
    -webkit-animation:
      fadein 0.3s ease 1.7s forwards,
      show-type-3 0.6s ease 1.7s forwards;
            animation:
      fadein 0.3s ease 1.7s forwards,
      show-type-3 0.6s ease 1.7s forwards;
  }
  
  
  /***************************************************************************************************
   *  keyframes
   ***************************************************************************************************/
  @-webkit-keyframes hidden-ball {
    0%   { -webkit-transform: scale(1); transform: scale(1); }
    50%  { -webkit-transform: scale(1.4); transform: scale(1.4); }
    100% { -webkit-transform: scale(0); transform: scale(0); }
  }
  @keyframes hidden-ball {
    0%   { -webkit-transform: scale(1); transform: scale(1); }
    50%  { -webkit-transform: scale(1.4); transform: scale(1.4); }
    100% { -webkit-transform: scale(0); transform: scale(0); }
  }
  @-webkit-keyframes show-type-1 {
    from { -webkit-transform: translateY(130px); transform: translateY(130px); }
    to   { -webkit-transform: translateY(-75px); transform: translateY(-75px); }
  }
  @keyframes show-type-1 {
    from { -webkit-transform: translateY(130px); transform: translateY(130px); }
    to   { -webkit-transform: translateY(-75px); transform: translateY(-75px); }
  }
  @-webkit-keyframes show-type-2 {
    from { -webkit-transform: translateY(170px); transform: translateY(170px); }
    to   { -webkit-transform: translateY(-40px); transform: translateY(-40px); }
  }
  @keyframes show-type-2 {
    from { -webkit-transform: translateY(170px); transform: translateY(170px); }
    to   { -webkit-transform: translateY(-40px); transform: translateY(-40px); }
  }
  @-webkit-keyframes show-type-3 {
    from { -webkit-transform: translateY(150px) rotate(0deg); transform: translateY(150px) rotate(0deg); }
    to   { -webkit-transform: translateY(-40px) rotate(270deg); transform: translateY(-40px) rotate(270deg); }
  }
  @keyframes show-type-3 {
    from { -webkit-transform: translateY(150px) rotate(0deg); transform: translateY(150px) rotate(0deg); }
    to   { -webkit-transform: translateY(-40px) rotate(270deg); transform: translateY(-40px) rotate(270deg); }
  }
  
  @-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
  }
  
  @keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
  }
  @-webkit-keyframes rotate360 {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to   { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  @keyframes rotate360 {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to   { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }

