@-webkit-keyframes high-bound {
  0%   { -webkit-transform: translateY(-300%); transform: translateY(-300%); opacity: 0; }
  40%  { -webkit-transform: translateY(0%); transform: translateY(0%); opacity: 1; }
  50%  { -webkit-transform: translateY(-20%) rotate(5deg); transform: translateY(-20%) rotate(5deg); opacity: 1; }
  60%  { -webkit-transform: translateY(0%) rotate(0deg); transform: translateY(0%) rotate(0deg); opacity: 1; }
  70%  { -webkit-transform: translateY(-10%) rotate(-2.5deg); transform: translateY(-10%) rotate(-2.5deg); opacity: 1; }
  80%  { -webkit-transform: translateY(0%) rotate(0deg); transform: translateY(0%) rotate(0deg); opacity: 1; }
  90%  { -webkit-transform: translateY(-5%) rotate(1.25deg); transform: translateY(-5%) rotate(1.25deg); opacity: 1; }
  100% { -webkit-transform: translateY(0%) rotate(0deg); transform: translateY(0%) rotate(0deg); opacity: 1; }
}

