/**
 * contain-box
 */
.contain-box {
  position: relative;
  width: 50%;
  height: 22%;
  transition: 1s; }
  .contain-box.super-me {
    height: 60%; }
    .contain-box.super-me.super-image {
      height: 90%; }
    .contain-box.super-me .image {
      -webkit-animation: remain-image 1s ease forwards;
              animation: remain-image 1s ease forwards; }
      .contain-box.super-me .image.content-image {
        -webkit-animation: content-image 1s ease forwards;
                animation: content-image 1s ease forwards; }
        .contain-box.super-me .image.content-image:hover:after {
          background-color: transparent; }
  .contain-box.no-me {
    display: none; }
  .contain-box * {
    box-sizing: border-box; }
  .contain-box .image {
    position: absolute;
    background-size: cover;
    -webkit-animation: image 4s ease 1s forwards;
            animation: image 4s ease 1s forwards;
    width: 0;
    height: 0;
    cursor: pointer; }
    .contain-box .image.content-image {
      background-size: contain; }
    .contain-box .image:after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      transition: 1s;
      border-radius: 15px; }
    .contain-box .image:hover:after {
      background-color: rgba(179, 170, 167, 0.164); }
  .contain-box .description {
    position: absolute;
    background-color: #fff;
    cursor: pointer;
    width: 96%;
    height: 66%;
    bottom: 10%;
    left: 2%;
    border-radius: 15px;
    border: solid 3px #fff; }
    .contain-box .description.disappear {
      display: none; }
    .contain-box .description .text {
      margin: 6px 12px; }
  .contain-box .spin {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-transform: scale(0);
            transform: scale(0); }
  .contain-box .spin:after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    border: solid 3px #fff;
    box-sizing: border-box; }

/******************************************************************
  animation
*******************************************************************/
.contain-box .spin:nth-child(1) {
  -webkit-animation: scale0-1 1.5s ease forwards;
          animation: scale0-1 1.5s ease forwards;
  -webkit-animation-delay: 1.1s;
          animation-delay: 1.1s; }
  .contain-box .spin:nth-child(1)::after {
    -webkit-animation: rotate180 2.5s ease forwards;
            animation: rotate180 2.5s ease forwards;
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
    opacity: 0.9; }

